export default {
  data() {
    return {
      title: {
        name: "其他区域",
        flag: false
      }
    };
  },
  methods: {
    goclick(index) {
      if (index == "1") {
        let region = {
          name:"澳门",
          code:"101330101"
        };
        sessionStorage.setItem("region", JSON.stringify(region));
        this.$router.go(-1); //返回上一次历史记录
      }
      if (index == "2") {
         let region = {
          name:"氹仔岛",
          code:"101330102"
        };
        sessionStorage.setItem("region", JSON.stringify(region));
        this.$router.go(-1); //返回上一次历史记录
      }
      if (index == "3") {
         let region = {
          name:"路环岛",
          code:"101330103"
        };
        sessionStorage.setItem("region", JSON.stringify(region));
        this.$router.go(-1); //返回上一次历史记录
      }
    }
  },mounted:function () {
     window.goBack = this.goBack;//返回
   }
};



